* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: rgb(63, 63, 63);
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Main_Cntnr {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Preview {
  width: 22%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.Preview_list {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(63, 63, 63);
  margin: 20% 0 0 5%;
  flex-direction: column;
  margin-left: 25px;
}
.Preview_list li {
  padding-bottom: 15px;
  font-size: 20px;
}

.leftbtns_Preview {
  margin-top: 50%;
}
.Emailbtn_Preview {
  width: 140px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008bdf;
  color: #fff;
  border: none;
  margin: 15px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.Supportbtn_Preview {
  width: 140px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192a3e;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 15px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.lftmail .fa-envelope,
.lftphone .fa-phone-alt {
  padding-right: 8px;
  color: #192a3e;
}
.lftmail a {
  color: #008bdf;

  text-decoration: none;
}
.lftmail,
.lftphone {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone {
  color: #192a3e;
  text-align: center;
  cursor: pointer;
}

.NavContainerPreview {
  width: 78%;
  height: auto;
  position: absolute;
  left: 22%;
}
.navbar_Preview {
  height: 50px;
  width: 77.8%;
  position: fixed;
  left: 22.2%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
  color: #008bdf;
}

.whole {
  width: 100%;
  position: absolute;
  top: 52px;
}
.My_Js_My_As {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.527);
}
.my_job {
  background-color: rgba(128, 128, 128, 0.527);
  color: grey;
  padding-left: 5px;
  display: flex;
  height: 70px;
  align-items: center;
}
.my_job h1,
.edit_jb h1 {
  font-size: 1.5em;
  font-weight: 100;
  cursor: pointer;
}
.arrow {
  width: 40px;
  height: 55px;
  font-weight: 100;
  line-height: 70px;
  background-color: #fff;
  border-left: 40px solid rgba(128, 128, 128, 0.527);
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
}
.edit_jb {
  margin-left: 20px;
  color: grey;
}
.Preview {
  padding: 20px 30px;
}
.Preview h2 {
  line-height: 40px;
  color: grey;
  font-weight: 100;
  font-size: 30px;
}
.Preview_p {
  padding: 20px 0;
}
.Preview_p p {
  font-size: 17px;
  line-height: 27px;
}
.SHgjd_edit_rdios {
  padding: 25px 0;
}
.rdio_editjb {
  padding-bottom: 5px;
  display: flex;
  align-items: center;
}
.rdio_editjb input {
  margin-right: 5px;
}
.Posting_info_box {
  border: 1px solid grey;
  height: auto;
  width: 100%;
}
.JD h3 {
  margin: 20px 0 0 30px;
  font-weight: 100;
}
.Job_descrption_box {
  height: 70vh;
  border: 1px solid grey;
  overflow-y: scroll;
  width: 95%;
  margin: auto auto 5px auto;
}
.PI {
  margin: 20px 0 0 30px;
}
.PI h3 {
  font-weight: 100;
}
.PI > div {
  margin-bottom: 15px;
}
