  }
  .my_job h1,
  .edit_jb h1 {
    font-size: 1.4em;
    padding: 0 5px;
  }
  .edit_jb {
    margin-left: 5px;
  }
  .arrow {
    width: 10px;
    height: 20px;

    border-left: 15px solid rgba(128, 128, 128, 0.527);
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
  }
  .Preview {
    padding: 12px 15px;
