  .Preview_p p {
    font-size: 8px;
    line-height: 15px;
  }
  .SHgjd_edit_rdios {
    padding: 15px 0;
  }
  .rdio_editjb {
    padding-bottom: 4px;
  }
  .rdio_editjb input {
    margin-right: 4px;
  }

  .JD h3 {
    margin: 10px 0 0 10px;
  }
  .Job_descrption_box {
    height: 70vh;
  }
  .PI {
    margin: 10px 0 0 10px;
  }

  .PI > div {
    margin-bottom: 8px;
  }
}
